import React, {Component} from 'react';
import './popup.css';
import PopupForm from "../popupForm";
import {connect, ConnectedProps} from "react-redux";
import {AppState} from "../../store";
import ImageService from "../../services/imageService";
import {closePopup, getComment, getImageDetails, hasError, sendComment} from "../../actions/action-creators";
import {CommentType, ImageDetailsType} from "../../types";
import Comment from "../comment";
import Preloader from "../preloader";
import {clearFields} from "redux-form";
import Error from "../error";

class Popup extends Component<PropsFromRedux> {

    componentDidMount(): void {
        let imageService = new ImageService();
        let {id, onGetImageDetails} = this.props;
        imageService.getFullImageData(id)
            .then( (data: ImageDetailsType) => onGetImageDetails(data))

    }

    handleFormSubmit = (values: any) => {
        this.props.onSendComment();
        let imageService = new ImageService();
        let {id} = this.props;
        let timeStamp: number = Date.now();
        imageService.postComment(values, id, timeStamp)
            .then( (data) => {
                this.props.onGetComment(data);
                this.props.clearForm()
            });
    };

    render() {
        let {image, onClosePopup, comments, hasError} = this.props;
        if (hasError) {
            return <Error />
        }
        let com;
        if (image && comments && comments.length !== 0) {
            let neededCom = comments.filter( (comment: CommentType & {name: string}) => {
                // @ts-ignore
                return comment.id === image.id
            })
            if (neededCom) {
                // @ts-ignore
                com = neededCom.map( (comm: CommentType & {name: string}, i: number) => <Comment key={i} {...comm}/>)
            }

        }
            return (
                <section className="popup">
                    <div className="container">
                        <div className="popup-block">
                            <div className="image-side">
                                {
                                    image ? <img src={image ? image.url : undefined} alt=""/> : <Preloader />
                                }
                            </div>
                            <PopupForm onSubmit={this.handleFormSubmit}/>
                            <div className="comments-side">
                                {
                                    !comments ? 'Комментариев пока нет' : comments.length === 0 ? 'Комментариев пока нет' : com
                                }
                            </div>
                            <div className="close-btn" onClick={() => onClosePopup()}>
                                <span></span>
                            </div>
                        </div>
                    </div>
                </section>
            )
    }
}

const mapStateToProps = (state: AppState):MapStateToProps => {
    return {
        id: state.popupReducer.currentImageId,
        load: state.popupReducer.loading,
        image: state.popupReducer.imageDetails,
        loadComment: state.popupReducer.sendingComment,
        comments: state.popupReducer.comments,
        hasError: state.galleryReducer.hasError
    }
};

const mapDispatchToProps = (dispatch: any):MapDispatchToProps => {
    return {
        onSendComment: () => dispatch(sendComment()),
        onClosePopup: () => dispatch(closePopup()),
        onGetImageDetails: (imageDetails: ImageDetailsType) => dispatch(getImageDetails(imageDetails)),
        onGetComment: (comment: CommentType & {name: string}) => dispatch(getComment(comment)),
        clearForm:() => dispatch(clearFields('commentForm', false, false, ...['name', 'comment'])),
        onGetError: () => dispatch(hasError())
    }
};

type MapStateToProps = {
    id: number | null,
    load: boolean,
    image: ImageDetailsType | null,
    loadComment: boolean,
    comments: Array<CommentType & {name: string}> | null,
    hasError: boolean,
}

type MapDispatchToProps = {
    onGetImageDetails: (imageDetails: ImageDetailsType) => any,
    onClosePopup: () => any,
    onSendComment: () => any,
    onGetComment: (comment: CommentType & {name: string}) => any,
    clearForm: () => any,
    onGetError: () => any,
}

let connector = connect(mapStateToProps, mapDispatchToProps);
type PropsFromRedux = ConnectedProps<typeof connector>

export default connector(Popup);