import React, {Component} from "react";
import './gallery.css';

import {connect, ConnectedProps} from 'react-redux';
import {AppState} from "../../store";
import {fetchAllImages, getImages, hasError} from "../../actions/action-creators";
import Preloader from "../preloader";
import ImageService from "../../services/imageService";
import {ImageType} from "../../types";
import Image from "../image";
import Error from "../error";

class Gallery extends Component<GalleryPropsType> {

    componentDidMount(): void {
        let imageService = new ImageService();
        let {onStartLoading, onGetImages} = this.props;
        onStartLoading();
        imageService.getAllImages()
            .then( (images: Array<ImageType>) => onGetImages(images))
            .catch( () => this.props.onGetError())
    }

    render() {
        let {images} = this.props;
        if (!images) {
            return <Preloader />
        }

        if (this.props.hasError) {
            return <Error />
        }
        return (
            <section className="gallery">
                <div className="container">
                    <div className="gallery__content">
                        {
                            images.map( image => <Image key={image.id} {...image}/>)
                        }
                    </div>
                </div>
            </section>
        )


    }
}

const mapStateToProps = (state: AppState): MapStateToPropsType => {
    return {
        images: state.galleryReducer.images,
        loading: state.galleryReducer.loading,
        hasError: state.galleryReducer.hasError
    }
};

const mapDispatchToProps = (dispatch: any): MapDispatchToPropsType => {
    return {
        onStartLoading: () => dispatch(fetchAllImages()),
        onGetImages: (images: Array<ImageType>) => dispatch(getImages(images)),
        onGetError: () => dispatch(hasError())
    }
};

type MapStateToPropsType = {
    images: Array<ImageType> | null,
    loading: boolean,
    hasError: boolean
}


type MapDispatchToPropsType = {
    onStartLoading: () => any,
    onGetImages: (images: Array<ImageType>) => any,
    onGetError: () => any,
}

const connector = connect(
    mapStateToProps,
    mapDispatchToProps
);

type GalleryPropsType = ConnectedProps<typeof connector>

export default connector(Gallery);