import React, {FC} from 'react';
import './popupForm.css';
import {reduxForm, Field, InjectedFormProps} from "redux-form";
import FormButton from "../form-button/form-button";

const PopupForm: FC<InjectedFormProps> = (props) => {
    return (
        <form className="popup-form" onSubmit={props.handleSubmit} >
            <Field required type="text" name="name" placeholder="Ваше имя" component="input"/>
            <Field required type="text" name="comment" placeholder="Ваш комментарий" component="input"/>
            <FormButton/>
        </form>
    )
};


export default reduxForm({form: "commentForm"})(PopupForm);