import React, {FC} from 'react';
import './image.css'
import {ImageType} from "../../types";
import {showPopup} from "../../actions/action-creators";
import {connect, ConnectedProps} from "react-redux";

const Image: FC<ImagePropsType> = (props) => {
    let {id, url, onShowPopup} = props;
    return (
        <div className="gallery__image" onClick={() => onShowPopup(id)}>
            <img src={url} alt=""/>
        </div>
    )
};

const mapDispatchToProps = (dispatch: any) => {
    return {
        onShowPopup: (id: number) => dispatch(showPopup(id))
    }
};


let connector = connect(null, mapDispatchToProps);
type PropsFromRedux = ConnectedProps<typeof connector>

type ImagePropsType = PropsFromRedux & ImageType & {
    key: any
}

export default connector(Image);