import React, {FC} from 'react';
import './error.css';
import img from './error-image.png';

const Error: FC = () => {
    return (
        <section className="error">
            <div className="container">
                <div className="error__content">
                    <img className="error__content-image" src={img} alt="error"/>
                    <h2 className="error__content-text">Что-то не так с Ваши интернет соединением</h2>
                </div>
            </div>
        </section>
    )
};

export default Error;