import {CLOSE_POPUP, GET_COMMENT, GET_IMAGE_DETAILS, SEND_COMMENT, SHOW_POPUP} from "../actions/action-types";
import {CommentType, ImageDetailsType} from "../types";

type PopupInitialStateType = {
    loading: boolean,
    isOpen: boolean,
    currentImageId: number | null,
    imageDetails: ImageDetailsType | null,
    sendingComment: boolean,
    comments: Array<CommentType & {name: string}> | null
}

let popupInitialState: PopupInitialStateType = {
    loading: false,
    isOpen: false,
    currentImageId: null,
    imageDetails: null,
    sendingComment: false,
    comments: null,
};

let popupReducer = (state: PopupInitialStateType = popupInitialState, action: any):PopupInitialStateType => {
    switch (action.type) {
        case SHOW_POPUP:
            return {
                ...state,
                isOpen: true,
                loading: true,
                currentImageId: action.payload
            };
        case GET_IMAGE_DETAILS:
            return {
                ...state,
                loading: false,
                imageDetails: action.payload
            };
        case CLOSE_POPUP:
            return {
                ...state,
                isOpen: false,
                imageDetails: null
            };
        case SEND_COMMENT:
            return {
                ...state,
                sendingComment: true
            };
        case GET_COMMENT:
            let newComments = [];
            if (!state.comments) {
                newComments = [action.payload]
            }
            else {
                newComments = [...state.comments, action.payload];
            }
            return {
                ...state,
                sendingComment: false,
                comments: newComments
            };
        default:
            return state;
    }
};

export default popupReducer;