import React, {FC} from 'react';
import './comment.css';
import {CommentType} from "../../types";

const Comment:FC<PropsType> = (props) => {
    let timestamp = props.date.toString();
    timestamp = timestamp.substr(0, timestamp.length - 3);
    let date = new Date(+timestamp * 1000);
    let day = date.getDate() < 10 ? `0${date.getDate()}` : date.getDate();
    let month = date.getMonth() + 1 < 10 ? `0${date.getMonth() + 1}` : date.getMonth() + 1;
    let year = date.getFullYear();
    let fullData = `${day}.${month}.${year}`;
    return (
        <div className="comment-item">
            <span className="date">{fullData}</span>
            <i className="comment-name">{props.name}:</i>
            <p className="comment-text">{props.comment}</p>
        </div>
    )
};

type PropsType = CommentType & {
    name: string
}


export default Comment;