import {CommentType, ImageDetailsType, ImageType} from "../types";
export const FETCH_ALL_IMAGES = 'FETCH_ALL_IMAGES';
export const GET_IMAGES = 'GET_IMAGES';
export const SHOW_POPUP = 'SHOW_POPUP';
export const CLOSE_POPUP = 'CLOSE_POPUP';
export const GET_IMAGE_DETAILS = 'GET_IMAGE_DETAILS';
export const SEND_COMMENT = 'SENT_COMMENT';
export const GET_COMMENT = 'GET_COMMENT';
export const HAS_ERROR = 'HAS_ERROR';

export type GetAllImagesActionType = {
    type: typeof GET_IMAGES,
    payload: Array<ImageType>
}

export type FetchAllImagesType = {
    type: typeof FETCH_ALL_IMAGES;
};

export type showPopupActionType = {
    type: typeof SHOW_POPUP;
    payload: number
};

export type closePopupActionType = {
    type: typeof CLOSE_POPUP;
};

export type GetImageDetailsActionType = {
    type: typeof GET_IMAGE_DETAILS,
    payload: ImageDetailsType
}

export type SendCommentActionType = {
    type: typeof SEND_COMMENT,
}

export type GetCommentActionType = {
    type: typeof GET_COMMENT,
    payload: CommentType & {name: string},
}

export type HasErrorActionType = {
    type: typeof HAS_ERROR,
}