import React, {FC} from 'react';
import './form-button.css';
import ButtonLoading from "../buttonLoading";
import {AppState} from "../../store";
import {connect, ConnectedProps} from "react-redux";

const FormButton: FC<PropsFromReduxF> = (props) => {
    return (
        <button>
            {
                props.loadComment ? <ButtonLoading /> : 'Оставить комментарий'
            }
        </button>
    )
};

let mapStateToProps = (state: AppState): MapStateToProps => {
    return {
        loadComment: state.popupReducer.sendingComment
    }
};

type MapStateToProps = {
    loadComment: boolean
}

let connector = connect(mapStateToProps);
type PropsFromReduxF = ConnectedProps<typeof connector>

export default connector(FormButton);