import {
    CLOSE_POPUP,
    closePopupActionType,
    FETCH_ALL_IMAGES,
    FetchAllImagesType,
    GET_COMMENT,
    GET_IMAGE_DETAILS,
    GET_IMAGES,
    GetAllImagesActionType,
    GetCommentActionType,
    GetImageDetailsActionType,
    HAS_ERROR,
    HasErrorActionType,
    SEND_COMMENT,
    SendCommentActionType,
    SHOW_POPUP,
    showPopupActionType
} from "./action-types";
import {CommentType, ImageDetailsType, ImageType} from "../types";

export const fetchAllImages = (): FetchAllImagesType => {
    return {
        type: FETCH_ALL_IMAGES
    }
};

export const getImages = (images: Array<ImageType>): GetAllImagesActionType => {
    return {
        type: GET_IMAGES,
        payload: images,
    }
};

export const showPopup = (id: number) :showPopupActionType => {
    return {
        type: SHOW_POPUP,
        payload: id
    }
};


export const closePopup = () :closePopupActionType => {
    return {
        type: CLOSE_POPUP,
    }
};

export const getImageDetails = (imageDetails: ImageDetailsType):GetImageDetailsActionType => {
    return {
        type: GET_IMAGE_DETAILS,
        payload: imageDetails
    }
};

export const sendComment = ():SendCommentActionType => {
    return {
        type: SEND_COMMENT,
    }
};

export const getComment = (comment: CommentType & {name: string}):GetCommentActionType => {
    return {
        type: GET_COMMENT,
        payload: comment
    }
};

export const hasError = ():HasErrorActionType => {
    return {
        type: HAS_ERROR,
    }
};