import React, {FC} from 'react';
import './App.css';
import Header from "./components/header";
import Footer from "./components/footer";
import Gallery from "./components/gallery";
import ErrorBoundary from "./components/errorBoundary";
import Popup from "./components/popup";
import {connect, ConnectedProps} from "react-redux";
import {AppState} from "./store";

const App: FC<PropsFromRedux> = (props) => {
    let {isPopup} = props;
  return (
    <div className="app">
        <Header />
        <ErrorBoundary>
            <Gallery />
        </ErrorBoundary>
        <div className="container">
            <p>
                It is a test task from Avito. <br/>
                Link: <a href="https://github.com/avito-tech/safedeal-frontend-trainee">Click here to read</a><br/>
                <i>Made by <a href="https://github.com/kyrsanter">kyrsanter</a></i>
            </p>
        </div>
        <Footer />
        {
            isPopup ? <Popup /> : null
        }
    </div>
  );
};

const mapStateToProps = (state: AppState) => {
    return {
        isPopup: state.popupReducer.isOpen
    }
};


let connector = connect(mapStateToProps);
type PropsFromRedux = ConnectedProps<typeof connector>;

export default connector(App);
