import {FETCH_ALL_IMAGES, GET_IMAGES, HAS_ERROR} from "../actions/action-types";
import {ImageType} from "../types";

type InitialGalleryState = {
    images: Array<ImageType> | null,
    loading: boolean,
    hasError: boolean
}

let initialGalleryState = {
    images: null,
    loading: false,
    hasError: false
};

let galleryReducer = (state: InitialGalleryState = initialGalleryState, action: any): InitialGalleryState => {
    switch (action.type) {
        case FETCH_ALL_IMAGES:
            return {
                ...state,
                loading: true,
            };
        case GET_IMAGES:
            return {
                ...state,
                loading: false,
                images: action.payload
            };
        case HAS_ERROR:
            return {
                ...state,
                hasError: true,
            }
        default:
            return state;
    }
};

export default galleryReducer;
