import {CommentType, ImageDetailsType, ImageType} from "../types";

class ImageService {

    private baseUrl = `https://boiling-refuge-66454.herokuapp.com/`;

    private getData = async (url: string) => {
        let response = await fetch(`${this.baseUrl}${url}`);
        if (response.ok) {
            return await response.json()
        }
        else {
            return new Error()
        }
    };

    public getAllImages = async ():Promise<Array<ImageType>> => {
        return await this.getData('images');
    };

    public getFullImageData = async (id: number | null):Promise<ImageDetailsType> => {
        return await this.getData(`images/${id}`);
    };

    public postComment = async (text: CommentType & {name: string}, id: number | null, date: number) => {
        let comment = {
            name: text.name,
            comment: text.comment,
            id,
            date
        };
        let url = `${this.baseUrl}images/${id}/comments`;
        try {
            return await fetch(url, {
                method: 'POST',
                body: JSON.stringify(comment),
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json',
                },
            }).then( (res) => comment);
        }
        catch (error){
            throw new Error(error);
        }
    }

}

export default ImageService;

